<template>
  <view class="containers">
    <lsDrag :list.sync="list" listKey="name" @dragSortChange="dragSortChange" @editItem="editItem" @deleteItem="deleteItem" />
  </view>
</template>

<script>
import lsDrag from './liusheng22-drag/liusheng22-drag'
export default {
  components: {
    lsDrag
  },
  data () {
    return {
      list: [
        { name: 'test1' },
        { name: 'test2' },
        { name: 'test3' },
        { name: 'test4' },
        { name: 'test5' },
        { name: 'test6' },
        { name: 'test7' },
        { name: 'test8' }
      ]
    }
  },
  methods: {
    // 拖拽排序触发
    dragSortChange (e) {
      console.log('drag', e)
      // e.frontData 插到谁后面
      // e.data 操作的数据
      let list = [...this.list]
      if (e.frontData) {
        let before = e.frontData.sort
        let after = e.data.sort
        list.splice(before, 0, list.splice(after, 1)[0])
      } else {
        list.splice(0, 0, list.splice(e.data.sort, 1)[0])
      }
      this.list = [...list]
    },
    // 编辑方法
    editItem (item) {
      // 做对应的编辑操作，如弹编辑框等
      console.log('edit item', item)
    },
    // 删除方法
    deleteItem (item) {
      // 做对应的删除操作，如调用删除接口等
      console.log('delete item', item)
      this.list.splice(item.index, 1)
    }
  }
}
</script>

<style lang="scss" scoped>
.containers {
  width: 750upx;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #f2f2f2;
}

</style>
